import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { ToastContainer, toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';  // Import for navigation
import PageTopBanner from "./PageTopBanner";

const formTitle = {
  title: "Submit a Request",
};

function InstructorRegistration() {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    postcode: '',
    transmission: 'Automatic',
    message: '',
    file: null,
  });
  const [fileName, setFileName] = useState('');  // Track file name
  const navigate = useNavigate();  // Initialize useNavigate hook for redirect

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setFormData({ ...formData, file });
    setFileName(file ? file.name : '');  // Set file name for display
  };

  const validateForm = () => {
    const { firstName, lastName, email, phone, postcode } = formData;
    if (!firstName || !lastName || !email || !phone || !postcode) {
      toast.error('Please fill in all required fields.');
      return false;
    }
    return true;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Validate form before submitting
    if (validateForm()) {
      toast.success('Form submitted successfully!');
      setTimeout(() => {
        navigate("/thankyou");
      }, 2000);
    }
  };

  return (
    <>
      <PageTopBanner title="Register to become an Instructor" />
      <div className="Registration bg-grey">
        <Container>
          <Row>
            <Col lg={12}>
              <div className="Registration__wrp shadow bg-white">
                <div className="Registration__left">
                  <h3>{formTitle.title}</h3>
                  
                  {/* Contact Form */}
                  <form className="RegistrationForm" onSubmit={handleSubmit}>
                    <div className="RegistrationForm__group">
                      <div className="RegistrationForm__field">
                        <label htmlFor="firstName">*First Name</label>
                        <input
                          type="text"
                          name="firstName"
                          id="firstName"
                          value={formData.firstName}
                          onChange={handleInputChange}
                          required
                        />
                      </div>
                      <div className="RegistrationForm__field">
                        <label htmlFor="lastName">*Last Name</label>
                        <input
                          type="text"
                          name="lastName"
                          id="lastName"
                          value={formData.lastName}
                          onChange={handleInputChange}
                          required
                        />
                      </div>
                    </div>

                    <div className="RegistrationForm__group">
                      <div className="RegistrationForm__field">
                        <label htmlFor="email">*Email Address</label>
                        <input
                          type="email"
                          name="email"
                          id="email"
                          value={formData.email}
                          onChange={handleInputChange}
                          required
                        />
                      </div>
                      <div className="RegistrationForm__field">
                        <label htmlFor="phone">*Phone Number</label>
                        <input
                          type="tel"
                          name="phone"
                          id="phone"
                          value={formData.phone}
                          onChange={handleInputChange}
                          required
                        />
                      </div>
                    </div>

                    <div className="RegistrationForm__field">
                      <label htmlFor="postcode">*Postcode</label>
                      <input
                        type="text"
                        name="postcode"
                        id="postcode"
                        value={formData.postcode}
                        onChange={handleInputChange}
                        required
                      />
                    </div>

                    <div className="RegistrationForm__field">
                      <label htmlFor="transmission">*Please select the transmission</label>
                      <select
                        name="transmission"
                        id="transmission"
                        value={formData.transmission}
                        onChange={handleInputChange}
                      >
                        <option value="Automatic">Automatic</option>
                        <option value="Manual">Manual</option>
                      </select>
                    </div>

                    <div className="RegistrationForm__field hasTextarea">
                      <label htmlFor="message">
                        Tell us more about yourself, the more information the better
                      </label>
                      <textarea
                        name="message"
                        id="message"
                        value={formData.message}
                        onChange={handleInputChange}
                      />
                    </div>
                    <p className="RegistrationForm__note">
                      Some information you include in your message may be considered sensitive by some privacy laws. By submitting your request, you give us consent to process any sensitive information included in your enquiry.
                    </p>
                    
                    {/* Custom File Input */}
                    <div className="RegistrationForm__field">
                      <label htmlFor="file">Attachments (Optional)</label>
                      <div className="fileWrp position-relative">
                        <p>
                          {fileName ? (
                            <span>Selected File: <strong>{fileName}</strong></span>
                          ) : (
                            <span><strong>UPLOAD FILE</strong> or drop files here</span>
                          )}
                        </p>
                        <input 
                          type="file" 
                          name="file" 
                          id="file" 
                          onChange={handleFileChange} 
                        />
                      </div>
                    </div>

                    <button type="submit" className="btn btn-blue">
                      Submit Form
                    </button>
                  </form>
                </div>
                <div className="Registration__right"></div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <ToastContainer />
    </>
  );
}

export default InstructorRegistration;
